define('RequestManager',[
		'NamespaceCore',
		'jquery'
	],
	function (G,
			  $) {
		'use strict';

		G.core.RequestManager = G.core.Class.extend({
			initialize: function () {
			},

			executeRequest: function (url, options) {
				options = options || {};
				options.type = options.type || 'GET';
				this._setValidDefaultOptions(options, options.type);
				return this._executeAsync(url, options);
			},

			get: function (url, options) {
				options = options || {};
				this._setValidDefaultOptions(options, 'GET');
				return this._executeAsync(url, options);
			},

			put: function (url, options) {
				options = options || {};
				this._setValidDefaultOptions(options, 'PUT');
				return this._executeAsync(url, options);
			},

			/**
			 * Shorthand method for executing a DELETE request
			 *
			 * @param {string} url - The url to put the data to
			 * @param {object} options - The options for the request
			 * 		TODO: add description for the options object
			 * @returns {promise} A jQuery style promise for the executed request
			 */
			delete: function (url, options) {
				options = options || {};
				this._setValidDefaultOptions(options, 'DELETE');
				return this._executeAsync(url, options);
			},

			post: function (url, options) {
				options = options || {};
				this._setValidDefaultOptions(options, 'POST');
				return this._executeAsync(url, options);
			},

			_setValidDefaultOptions: function (options, requestType) {
				if (options === undefined || options === null) {
					options = {};
				}

				if (options.type === undefined || options.type === null || options.type.toUpperCase() !== requestType.toUpperCase()) {
					options.type = requestType.toUpperCase();
				}
			},

			_executeAsync: function (url, options) {
				var requestOptions = {
					url: url,
					xhrFields: {
						withCredentials: true
					}
				};

				if (options !== undefined && options !== null && options !== {}) {
					//Merge custom options over defaults if provided
					requestOptions = $.extend(requestOptions, options);
				}

				return $.ajax(requestOptions);
			}
		});

		G.core.Util.addMethod(G.core.RequestManager.prototype, 'get', function (url) {
			return this.get(url, null);
		});

		return G;
	});

