define('Util',['NamespaceCore'], function (G) {
	'use strict';

	G.core.Util = {
		/*********************************/
		/***** G.core custom methods *****/
		/*********************************/

		/**
		 * Used for method overloading in Javascript
		 *
		 * @param object
		 * @param name
		 * @param fn
		 */
		addMethod: function (object, name, fn) {
			var old = object[name];
			object[name] = function () {
				if (fn.length === arguments.length) {
					return fn.apply(this, arguments);
				} else if (typeof old === 'function') {
					return old.apply(this, arguments);
				}
			};
		},

		/***************************/
		/***** Default methods *****/
		/***************************/
		/**
		 * (Object[, Object, ...]) ->
		 *
		 * @param dest
		 * @returns {*}
		 */
		extend: function (dest) {
			var sources = Array.prototype.slice.call(arguments, 1),
				i, j, len, src;

			for (j = 0, len = sources.length; j < len; j++) {
				src = sources[j] || {};
				for (i in src) {
					if (src.hasOwnProperty(i)) {
						dest[i] = src[i];
					}
				}
			}
			return dest;
		},

		/**
		 * (Function, Object) -> Function
		 *
		 * @param fn
		 * @param obj
		 * @returns {Function}
		 */
		bind: function (fn, obj) {
			var args = arguments.length > 2 ? Array.prototype.slice.call(arguments, 2) : null;
			return function () {
				return fn.apply(obj, args || arguments);
			};
		},

		invokeEach: function (obj, method, context) {
			var i, args;

			if (typeof obj === 'object') {
				args = Array.prototype.slice.call(arguments, 3);

				for (i in obj) {
					if (obj.hasOwnProperty(i)) {
						method.apply(context, [i, obj[i]].concat(args));
					}
				}
				return true;
			}

			return false;
		},

		limitExecByInterval: function (fn, time, context) {
			var lock, execOnUnlock;

			return function () {
				var args = arguments;

				if (lock) {
					execOnUnlock = true;
					return;
				}

				lock = true;
				var self = this;

				setTimeout(function () {
					lock = false;

					if (execOnUnlock) {
						self.apply(context, args);
						execOnUnlock = false;
					}
				}, time);

				fn.apply(context, args);
			};
		},

		falseFn: function () {
			return false;
		},

		formatNum: function (num, digits) {
			var pow = Math.pow(10, digits || 5);
			return Math.round(num * pow) / pow;
		},

		trim: function (str) {
			return str.trim ? str.trim() : str.replace(/^\s+|\s+$/g, '');
		},

		splitWords: function (str) {
			return G.Util.trim(str).split(/\s+/);
		},

		setOptions: function (obj, options) {
			obj.options = G.extend({}, obj.options, options);
			return obj.options;
		},

		template: function (str, data) {
			return str.replace(/\{ *([\w_]+) *\}/g, function (str, key) {
				var value = data[key];
				if (value === undefined) {
					throw new Error('No value provided for variable ' + str);
				} else if (typeof value === 'function') {
					value = value(data);
				}
				return value;
			});
		},

		isArray: Array.isArray || function (obj) {
			return (Object.prototype.toString.call(obj) === '[object Array]');
		}
	};

	// shortcuts for most used utility functions
	G.extend = G.core.Util.extend;
	G.bind = G.core.Util.bind;
	G.stamp = G.core.Util.stamp;
	G.setOptions = G.core.Util.setOptions;

	return G;
});

